<template>
  <div class="goodsinfo-container">
    <!--  -->
    <transition @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
      <div class="ball" v-show="ballFlag" ref="ball"></div>
    </transition>
    <!--  -->
    <div class="mui-card">
      <div class="mui-card-content">
        <swipe :swipeItemList="swipeItemList" :isfull="false"></swipe>
      </div>
    </div>
    <!--  -->
    <div class="mui-card" ref="test">
      <div class="mui-card-header"></div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <div class="price">
            
            <del>2399.00</del>&nbsp;&nbsp;
            
            <span class="now_price">2199</span>
          </div>
          <div>
            <span></span>
            <div ref="number" class="buy_number">
              <el-input-number
                v-model="num"
                size="mini"
                @change="handleChange"
                :min="1"
                :max="10"
                label=""
              ></el-input-number>
            </div>
          </div>
          <div>
            <mt-button type="primary" size="small"></mt-button>
            <mt-button type="danger" size="small" @click="addToShopcar"></mt-button>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="mui-card">
      <div class="mui-card-header"></div>
      <div class="mui-card-content">
        <div
          class="mui-card-content-inner"
        ></div>
      </div>
    </div>
  </div>
</template>

<script>
import swipe from '../subcomponents/Swipe.vue'

export default {
  data() {
    return {
      swipeItemList: [],
      num: 1,
      ballFlag: false
    }
  },
  props: ["id"],
  components: {
    swipe
  },
  created() {
    this.getSwipe()
  },
  mounted() {
    // 
    var numberBox = this.$refs.number.getBoundingClientRect()
    var top = numberBox.top + numberBox.height / 2
    var left = numberBox.left + numberBox.width / 2
    this.$refs.ball.style.top = `${top}px`
    this.$refs.ball.style.left = `${left}px`
  },
  updated() {
    // 
    var numberBox = this.$refs.number.getBoundingClientRect()
    var top = numberBox.top + numberBox.height / 2
    var left = numberBox.left + numberBox.width / 2
    this.$refs.ball.style.top = `${top}px`
    this.$refs.ball.style.left = `${left}px`
  },
  methods: {
    // 
    getSwipe() {
      this.$axios({
        method: 'get',
        url: 'http://192.168.43.49:5000/swipeitems'
      }).then(
        res => {
          console.log('swipeitems ', res.data)
          this.swipeItemList = res.data
        }
      ).catch(
        err => {
          console.log('swipeitems ', err)
        }
      )
    },
    handleChange(value) {
      console.log(value)
    },
    addToShopcar() {
      this.ballFlag = !this.ballFlag
    },
    beforeEnter: function (el) {
      // 
      el.style.transform = "translate(0, 0)"
    },
    enter: function (el, done) {
      // 
      var ballPosition = this.$refs.ball.getBoundingClientRect()
      // 
      var badgePosition = document.getElementById("badge").getBoundingClientRect()
      // 
      var x = badgePosition.left - ballPosition.left
      // 
      var y = badgePosition.top - ballPosition.top
      // el.offsetWidth
      el.style.transform = `translate(${x}px, ${y}px)`
      el.style.transition = "all 1s cubic-bezier(.24,-0.38,.71,-0.01)"
      done()
    },
    afterEnter: function (el) {
      // 
      this.ballFlag = !this.ballFlag
    }
  }
}
</script>

<style scoped>
.now_price {
  color: red;
  font-size: 16px;
  font-weight: bold;
}
.goodsinfo-container {
  overflow: hidden;
}
.ball {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: red;
  position: absolute;
  z-index: 2;
  transform: translate();
}
.buy_number {
  display: inline-block;
}
</style>
